// =============================================================================
// My account - Dialog
// =============================================================================

.#{$wpfs} {
  $root: &;

  &-dialog {
    background: #fff;
    max-width: 600px;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    outline: 0;
    z-index: 101;
    padding: spacer(4) spacer(3);
    border-radius: var(--default-border-radius);
    margin: spacer(3);

    .ui-dialog-titlebar-close {
      display: none;
    }

    .ui-dialog-titlebar {
      color: var(--primary-color);
      font-size: 20px;
      font-weight: bold;
      margin-bottom: spacer(2);
    }
  }

  &-dialog-overlay {
    background: rgba(47, 47, 55, .9);
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 100;
  }

  &-dialog-content-actions {
    margin-top: spacer(3);
  }

  &-dialog-content-text {
    color: #{color('gun-metal')};
    line-height: 24px;
    font-size: 15px;
    margin-bottom: spacer(1);
  }
}
